*{
    box-sizing: border-box;
    font-size: 16px;
    font-family: sans-serif;
}
:root{
    --color-blue: #287AE6;
}
a{
    color: var(--color-blue);
    text-decoration: none;
}
body{
    margin: 0;
    padding: 0;
    display: grid;
    place-items: center;
    min-height: 100vh;
}
.login_wrapper{
    width: 450px;
    height: 500px;
    border: 1px solid rgba(0, 0, 0, 0.171);
    border-radius: 10px;
    padding-top: 50px;
    padding-bottom: 50px;
}
.sign_title{
    text-align: center;
    font-size: 24px;
    padding-top: 15px;
    padding-bottom: 12px;
}
.login_wrapper .use{
    text-align: center;
    font-size: 16px;
}
.logo_google{
    background: url("https://ssl.gstatic.com/images/branding/googlelogo/1x/googlelogo_color_112x36dp.png") center no-repeat;
    height: 40px;   
}
.google_nikhil_form{
    width: 90%;
    margin: auto;
    padding-top: 50px;
}
.google_nikhil_form .email_wrapper{
    position: relative;
}
.google_nikhil_form .enter_email{
    width: 100%;
    padding: 16px 10px;
    border: 2px solid rgba(0, 0, 0, 0.11);
    outline: none;
    border-radius: 8px;
    margin-bottom: 10px;
    font-size: 17px;
    background-color: white;
}
.google_nikhil_form .enter_email:focus{
    border: 2px solid var(--color-blue);
}

.google_nikhil_form .enter_email:focus ~ #email_place, 
.google_nikhil_form .enter_email:valid ~ #email_place{
    top: -10px;
    font-size: 12px;
    color: var(--color-blue);
}
.google_nikhil_form #email_place{
    position: absolute;
    left: 10px;
    top: 25%;
    transition: .1s;
    background: white;
    color: rgba(0, 0, 0, 0.637);
    padding: 0px 6px;
    pointer-events: none;
    user-select: none;
    font-size: 17px;
}
.google_nikhil_form .forget{
    padding-bottom: 50px;
}
.google_nikhil_form .other__{
    padding-bottom: 30px;
}
.google_nikhil_form .next_item{
    display: flex;
    justify-content: space-between;
}
.google_nikhil_form .next_item .next_btn{
    padding: 8px 30px;
    background: var(--color-blue);
    color: white;
    border: none;
    outline: none;
    border-radius: 8px;
    cursor: pointer;
    transition: .1s;
}
.google_nikhil_form .next_item .next_btn:hover{
    opacity: .9;
}
@media screen and (max-width: 500px){
    .login_wrapper{
        border: none;
        width: 100%;
    }
}